/*******************************************************************************
 * Modern Minimal Themes - Light and Dark Mode
 ******************************************************************************/

:root {
 // Backgrounds
 --global-bg-color: #{$white-color};
 --global-bg-secondary: #{$grey-50};
 --global-code-bg-color: #{$code-bg-color-light};
 --global-card-bg-color: #{$white-color};

 // Text
 --global-text-color: #{$grey-900};
 --global-text-color-light: #{$grey-500};
 --global-text-color-muted: #{$grey-400};

 // Theme & Accent
 --global-theme-color: #{$primary-color};
 --global-hover-color: #{$accent-color};
 --global-hover-text-color: #{$white-color};
 --global-accent-color: #{$accent-color};

 // Borders & Dividers
 --global-divider-color: #{$grey-200};
 --global-border-color: #{$grey-200};

 // Footer
 --global-footer-bg-color: #{$grey-900};
 --global-footer-text-color: #{$grey-400};
 --global-footer-link-color: #{$white-color};

 // Other
 --global-distill-app-color: #{$grey-500};

 // Shadows
 --global-shadow-sm: #{$shadow-sm};
 --global-shadow-md: #{$shadow-md};
 --global-shadow-lg: #{$shadow-lg};

 // Block quotes - Modern minimal style
 --global-tip-block: #10B981;
 --global-tip-block-bg: #ECFDF5;
 --global-tip-block-text: #065F46;
 --global-tip-block-title: #059669;
 --global-warning-block: #F59E0B;
 --global-warning-block-bg: #FFFBEB;
 --global-warning-block-text: #92400E;
 --global-warning-block-title: #D97706;
 --global-danger-block: #EF4444;
 --global-danger-block-bg: #FEF2F2;
 --global-danger-block-text: #991B1B;
 --global-danger-block-title: #DC2626;

 .fa-sun {
 display: none;
 }
 .fa-moon {
 padding-left: 10px;
 padding-top: 12px;
 display: block;
 }

 .repo-img-light {
 display: block;
 }
 .repo-img-dark {
 display: none;
 }
}

html[data-theme='dark'] {
 // Backgrounds
 --global-bg-color: #{$grey-color-dark};
 --global-bg-secondary: #1A1F25;
 --global-code-bg-color: #{$code-bg-color-dark};
 --global-card-bg-color: #1A1F25;

 // Text
 --global-text-color: #E7E9EA;
 --global-text-color-light: #8899A6;
 --global-text-color-muted: #6B7B8A;

 // Theme & Accent
 --global-theme-color: #{$accent-color-light};
 --global-hover-color: #{$accent-color};
 --global-hover-text-color: #{$white-color};
 --global-accent-color: #{$accent-color-light};

 // Borders & Dividers
 --global-divider-color: #2F3542;
 --global-border-color: #2F3542;

 // Footer
 --global-footer-bg-color: #0A0D10;
 --global-footer-text-color: #8899A6;
 --global-footer-link-color: #{$white-color};

 // Other
 --global-distill-app-color: #8899A6;

 // Block quotes (keep consistent in dark mode)
 --global-tip-block: #34D399;
 --global-tip-block-bg: #064E3B;
 --global-tip-block-text: #A7F3D0;
 --global-tip-block-title: #6EE7B7;
 --global-warning-block: #FBBF24;
 --global-warning-block-bg: #78350F;
 --global-warning-block-text: #FDE68A;
 --global-warning-block-title: #FCD34D;
 --global-danger-block: #F87171;
 --global-danger-block-bg: #7F1D1D;
 --global-danger-block-text: #FECACA;
 --global-danger-block-title: #FCA5A5;

 .fa-sun {
 padding-left: 10px;
 padding-top: 12px;
 display: block;
 }
 .fa-moon {
 display: none;
 }

 .repo-img-light {
 display: none;
 }
 .repo-img-dark {
 display: block;
 }
}
